import { FaChevronLeft } from "react-icons/fa6";
import { useSidebar } from "../contexts/sidebarContext";
import { useBrands, useFeaturedBrands } from "../services/brandService";
import { useMemo, useRef, useState } from "react";
import { FiSearch } from "react-icons/fi";
import { Link } from "react-router-dom";
import Skeleton from "./Skeleton";
import Loading from "./Loading";

const SearchBar = ({ value, onChange }) => (
  <div className="BRAND-SEARCH">
    <form onSubmit={(e) => e.preventDefault()} className="relative">
      <input
        type="search"
        id="brand-search"
        value={value}
        onChange={onChange}
        placeholder="Search Brands"
        className="w-[95%] border border-primary outline-none text-xs py-1 pl-2 pr-4 rounded-tl-lg rounded-bl-lg"
      />
      <button className="p-1 border border-primary rounded-full absolute right-0 top-1/2 -translate-y-1/2 bg-white">
        <FiSearch size={18} />
      </button>
    </form>
  </div>
);

const MobileBrandList = () => {
  const { closeSidebar } = useSidebar();
  const { data: brands = [], isLoading: brandsLoading } = useBrands();
  const { data: featuredBrands = [], isLoading: featuredBrandsLoading } =
    useFeaturedBrands();
  const [searchedBrandName, setSearchedBrandName] = useState("");
  const brandListRef = useRef(null);

  const handleSearch = (e) => {
    setSearchedBrandName(e.target.value);
  };

  const listOfBrands = useMemo(() => {
    const filteredBrands = brands
      .filter((brand) =>
        brand.name.toLowerCase().includes(searchedBrandName.toLowerCase())
      )
      .sort((a, b) => a.name.localeCompare(b.name));

    return filteredBrands.reduce((acc, brand) => {
      const firstLetter = brand.name[0].toUpperCase();
      const key = /[A-Z]/.test(firstLetter) ? firstLetter : "â˜…";
      if (!acc[key]) {
        acc[key] = [];
      }
      acc[key].push(brand);
      return acc;
    }, {});
  }, [brands, searchedBrandName]);

  return (
    <div className="bg-white h-svh flex flex-col">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={closeSidebar} />
        <p className="text-lg font-medium">Brands</p>
      </div>

      <div className="BODY h-0 flex-grow flex flex-col">
        <div className="TOP bg-white flex flex-col space-y-4 h-60 flex-shrink-0 p-4">
          <SearchBar value={searchedBrandName} onChange={handleSearch} />
          <div ref={brandListRef} className="overflow-y-auto space-y-2">
            {brandsLoading ? (
              <ul className="min-h-full space-y-2">
                <div className="w-20 h-3 rounded my-4 overflow-hidden">
                  <Skeleton />
                </div>
                {Array.from({ length: 10 }).map((_, index) => (
                  <li key={index} className="w-30 h-2 rounded overflow-hidden">
                    <Skeleton />
                  </li>
                ))}
              </ul>
            ) : (
              Object.keys(listOfBrands).map((key) => (
                <ul key={key} className="overflow-y-auto">
                  <h4 id={`brand-heading-${key.toLowerCase()}`}>{key}</h4>
                  {listOfBrands[key].map((brand) => (
                    <li
                      key={brand._id}
                      className="text-xs text-secondary hover:text-black transition-colors duration-150 ease-in-out"
                    >
                      <Link to={`/brand/${brand._id}`} onClick={closeSidebar}>
                        {brand.name}
                      </Link>
                    </li>
                  ))}
                </ul>
              ))
            )}
          </div>
        </div>

        <div className="BOTTOM relative bg-lightGray h-full overflow-y-scroll">
          {featuredBrandsLoading ? (
            <Loading />
          ) : (
            <ul className="BRAND-LIST p-8 grid grid-cols-4 gap-4">
              {featuredBrands.map((brand) => (
                <Link
                  key={brand.name || brand._id}
                  to={`/brand/${brand._id}`}
                  onClick={closeSidebar}
                  className="relative flex items-center justify-center flex-shrink-0"
                >
                  <img
                    src={brand.logoUrl}
                    alt={brand.name}
                    loading="lazy"
                    className="size-20 object-contain object-center z-[1]"
                  />
                </Link>
              ))}
            </ul>
          )}
        </div>
      </div>
    </div>
  );
};

export default MobileBrandList;
